body {
  margin: 0;
}

.app-container {
  box-sizing: border-box;
}

.icon-container {
  background-color: #e5e5e5;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.text-padding-bottom {
  padding-bottom: 36px;
}

.text-padding-top {
  padding-top: 36px;
}

.toggle-container {
  box-sizing: border-box;
  background: linear-gradient(#f6b868 0%, #ee6b2d 100%);
  border: 1px dashed #7b61ff;
  border-radius: 5px;
  flex-direction: column;
  place-content: center space-around;
  width: 80%;
  transition: filter .5s ease-in;
  display: flex;
  position: relative;
}

.toggle-container-text {
  text-align: center;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
  font-weight: 700;
}

.question-text {
  margin: 35px 25px 25px;
}

.footer-text {
  margin: 25px;
}

.toggle-option-container-horiz {
  border: 2px solid #f9d29f;
  border-radius: 100px;
  height: 80px;
  margin: 10px 0;
  position: relative;
}

.toggle-option-horiz {
  justify-content: space-around;
  align-items: center;
  height: 80px;
  display: flex;
  position: absolute;
  inset: 0%;
}

.selected-box-horiz {
  background: #f8caa3;
  border-radius: 100px;
  justify-content: center;
  align-items: center;
  height: 80px;
  transition: transform .5s ease-out;
  position: absolute;
  inset: 0%;
}

.toggle-option-container-vert {
  border: 2px solid #f9d29f;
  border-radius: 24px;
  flex-direction: column;
  margin: 10px 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

.toggle-option-vert {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0%;
}

.selected-box-vert {
  background: #f8caa3;
  width: 100%;
  transition: transform .5s ease-out;
  position: absolute;
  inset: 0%;
}

.toggle-text {
  color: #fff;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 700;
  transition: color .5s;
}

.toggle-text-selected {
  color: #000;
  cursor: pointer;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 700;
  transition: color .5s;
}

